@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

body {
    font-family: 'Poppins', sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: stretch;
  height: fit-content;
  width: fit-content;
  padding: 5px;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: stretch;
  padding: 0;
  height: fit-content;
  width: fit-content;
  border-inline: 1px solid black;
}

.cell {
  border: 1px solid black;
  width: 5rem;
  height: 5rem;
  background-color: lightcyan;
}

.alternate_cell{
  background-color: white;
}

.cell:hover {
  background-image: url(./images/knight.PNG);
  background-size: cover;
  opacity: 10%;
}

.legend {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.legend > p {
  font-weight: bolder;
  font-size: 1em;
}

.legend div {
  min-width: 20px;
  min-height: 20px;
  background-color: green;
}